import React from "react";
import { Line } from "react-chartjs-2";
import Chart from 'chart.js/auto';

import {
  Chart as ChartJS,
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement

} from 'chart.js'

ChartJS.register(
  LineElement,
  CategoryScale,
  LinearScale,
  PointElement
)

const LineChart = () => {
  const state= {
    labels: ["China", "India", "United States","Pakistan","Brazil","Nigeria","Bangladesh", "Russia", "Mexico", "Japan","Ethiopia", "Philippines","Egypt", "Vietnam", "Turkey"],
    datasets: [
      {
        label: "Population",
        
        borderWidth: 5, 
        data: [38, 28, 38, 30, 43,23, 33, 18, 28,40,29, 26,25,32,32 ],
        backgroundColor: 'aqua',
        borderColor:'aqua',
        pointBorderColor: 'orange'

      }
    ]
  }
  const options = {
    plugins: {
      legend: {}
    }
  }
  return (
    <div>
      <p>Median Age in Countries</p>
      <Line 
      data ={state}
      options = {options}
      >

      </Line>
      
    </div>

  )
}


    
export default LineChart;